import { Pipe, PipeTransform } from '@angular/core';
import { environment } from '../environments/environment';

@Pipe({
    name: 'icomLibsCoreGetEnv',
    standalone: true
})
export class IcomLibsCoreEnvironmentPipe implements PipeTransform {
  transform(key: string, ...args: unknown[]): unknown {
    return this.getEnvValue(key);
  }

  private getEnvValue(key: string): any {
    return (environment as any)?.[key];
  }
}